body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:whitesmoke;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-wrapper {
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: cover;

  font-family: "Merriweather", serif;
  text-decoration: none;

  margin: 0 auto;
  display: grid;
  width: 100%;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-template-areas:
    "h h"
    "n c";
}

.app-wrapper-content {
  grid-area: c;
}

.Navbar_nav__3M6GH {
  grid-area: n;
  padding: 30px;
}

.Navbar_item__Jzo_K {
  text-decoration: none;
  font-size: larger;
  margin: 50px;
  color: #000000;
  text-align: left;
  font-weight: bolder;
  border-bottom: 2px solid #f2f3f7;
  font-family: "SegoeUIBold";
  font-size: 16px;
  text-transform: uppercase;
}

.Navbar_item__Jzo_K .Navbar_active__jpPnL {
  font-family: "Merriweather", serif;
  font-size: larger;
  margin: 50px;
  color: #1e3685;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  text-decoration: none;
  border-bottom: 2px solid #1e3685;
}

.Header_header__3lY3w {
  grid-area: h;
  flex-wrap: wrap;
}

.Header_headerName__uf2rp {
  padding: 25px 25px;
}

.Header_header__3lY3w img {
  height: 100px;
  padding: 10px 10px;
  float: left;
}

.Header_loginBlock__1U5jx {
  float: right;
  margin-right: 20px;
  margin-top: -100px;
  color: #000000;
  text-transform: uppercase;
  font-weight: bolder;
}

.Header_loginBlock__1U5jx a {
	text-decoration: none;
}

.preloader_loader__CJJGm {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

